<template>
  <div id="step2">

    <div>
      <p class="flow-text center-align">Lets check for a provider in your area.</p>
      <p class="flow-text center-align">&nbsp;</p>
    </div>

    <div class="progress">
      <div class="determinate" style="width:66%"></div>
    </div>

    <form @submit.prevent="processStep">

      <div class="input-field">
        <label for="address" v-bind:class="{ active: this.address }">Street address</label>
        <input type="text" name="address" id="address" class="validate" v-model="address">
      </div>

      <div class="input-field">
        <label for="city" v-bind:class="{ active: this.city }">City</label>
        <input type="text" name="city" id="city" class="validate" v-model="city">
      </div>

      <div class="input-field">
        <label for="state" v-bind:class="{ active: this.state }">State</label>
        <input type="text" name="state" id="state" class="validate" v-model="state">
      </div>

      <div class="input-field">
        <label for="zipcode" v-bind:class="{ active: this.zipcode }">zipcode</label>
        <input type="tel" name="zipcode" id="zipcode" v-model="zipcode">
      </div>

      <div class="input-field center-align">
        <button type="submit" class="btn waves-effect waves-teal">Next</button>
      </div>

    </form>

  </div>  
</template>

<script>
export default {
  name: 'step2',
  data () {
    return {
      address: null,
      city: null,
      state: null,
      zipcode: null
    }
  },
  created () {
    this.$bus.$on('zipLookupComplete', this.updateData)
    this.updateData()
  },
  destroy () {
    this.$bus.$off('zipLookupComplete', this.updateData)
  },
  methods: {
    processStep () {
      this.$formData.address = this.address
      this.$formData.city = this.city
      this.$formData.state = this.state
      this.$formData.zipcode = this.zipcode
      this.$router.push({ name: 'step3' })
    },
    updateData () {
      if (this.$formData.address) {
        this.address = this.$formData.address
      }
      if (this.$formData.city) {
        this.city = this.$formData.city
      }
      if (this.$formData.state) {
        this.state = this.$formData.state
      }
      if (this.$formData.zipcode) {
        this.zipcode = this.$formData.zipcode
      }
    }
  }
}
</script>

<style>
</style>



