<template>
  <div id="step1">

    <div>
      <p class="flow-text center-align">New FDA approved, non surgical weight loss.</p>
      <p class="flow-text center-align">See if you qualify for $1500 off!</p>
    </div>

    <div class="progress">
      <div class="determinate" style="width:33%"></div>
    </div>

    <form @submit.prevent="processStep">

      <div class="input-field">
        <label for="weight" v-bind:class="{ active: this.weight }">Enter your weight in lbs</label>
        <input type="tel" name="weight" id="weight" class="validate" v-model="weight" autofocus>
      </div>

      <div>
        <label>Select your height</label>
        <select class="browser-default" v-model="height">
          <option value="60">5' 0"</option>
          <option value="61">5' 1"</option>
          <option value="62">5' 2"</option>
          <option value="63">5' 3"</option>
          <option value="64">5' 4"</option>
          <option value="65">5' 5"</option>
          <option value="66">5' 6"</option>
          <option value="67">5' 7"</option>
          <option value="68">5' 8"</option>
          <option value="69">5' 9"</option>
          <option value="70">5' 10"</option>
          <option value="71">5' 11"</option>
          <option value="72">6' 0"</option>
          <option value="73">6' 1"</option>
          <option value="74">6' 2"</option>
          <option value="75">6' 3"</option>
          <option value="76">6' 4"</option>
          <option value="77">6' 5"</option>
          <option value="78">6' 6"</option>
        </select>
      </div>

      <div class="input-field">
        <label for="zipcode" v-bind:class="{ active: this.zipcode }">Enter your zipcode</label>
        <input type="tel" name="zipcode" id="zipcode" v-model="zipcode" @change="zipcodeChange">
      </div>

      <div class="input-field center-align">
        <button type="submit" class="btn waves-effect waves-teal">Continue</button>
      </div>

    </form>
  </div>  
</template>

<script>
export default {
  name: 'step1',
  data () {
    return {
      weight: null,
      height: '68',
      zipcode: null
    }
  },
  created () {
    if (this.$formData.weight) {
      this.weight = this.$formData.weight
    }
    if (this.$formData.height) {
      this.height = this.$formData.height
    }
    if (this.$formData.zipcode) {
      this.zipcode = this.$formData.zipcode
    }
  },
  methods: {
    processStep () {
      this.$formData.weight = this.weight
      this.$formData.height = this.height
      this.$formData.zipcode = this.zipcode
      this.$router.push({ name: 'step2' })
    },
    zipcodeChange () {
      this.lookupZip(this.zipcode)
    },
    lookupZip (zip) {
      var url = this.generateOpenCageDataUrlZip(zip)
      var self = this

      fetch(url)
      .then(function (response) {
        return response.json()
      })
      .then(function (location) {
        self.$formData.city = location.results[0].components.city
        self.$formData.state = location.results[0].components.state
        self.$formData.country = location.results[0].components['ISO_3166-1_alpha-2']
        self.$bus.$emit('zipLookupComplete')
      })
    },
    generateOpenCageDataUrlZip (zip) {
      var key = '41886799cfe34e87af44a27cf6ab3107'
      return 'https://api.opencagedata.com/geocode/v1/json?key=' + key + '&q=' + zip + '&no_annotations=1&limit=1'
    }
  }
}
</script>

<style>
</style>



