<template>
  <div id="step2">

    <div>
      <p class="flow-text center-align">Great news! Last step.</p>
      <p class="flow-text center-align">&nbsp;</p>
    </div>

    <div class="progress">
      <div class="determinate" style="width:100%"></div>
    </div>

    <form @submit.prevent="processStep">

      <div class="input-field">
        <label for="first-name">First name</label>
        <input type="text" name="first-name" id="first-name" class="validate">
      </div>

      <div class="input-field">
        <label for="last-name">Last name</label>
        <input type="text" name="last-name" id="last-name" class="validate">
      </div>

      <div class="input-field">
        <label for="email">Email address</label>
        <input type="email" name="email" id="email" class="validate">
      </div>

      <div class="input-field">
        <label for="phone">Phone number</label>
        <input type="tel" name="phone" id="phone" class="validate">
      </div>

      <div>
        <input type="checkbox" name="agree-terms" id="agree-terms" class="filled-in">
        <label for="agree-terms">
          <span class="flow-text">By submitting this form I agree to be contacted and to HIPPA waiver.</span>
        </label>        
      </div>

      <div class="input-field center-align">
        <button type="submit" class="btn waves-effect waves-teal">I'm ready to start losing</button>
      </div>

    </form>

  </div>  
</template>

<script>
export default {
  name: 'step3',
  data () {
    return {
    }
  },
  mounted () {
    window.Materialize.updateTextFields()
  },
  methods: {
    processStep () {
      this.$router.push({ name: 'thankyou' })
    }
  }
}
</script>

<style>
</style>



